"use client";

import React, { useState } from "react";

// PostComposer.tsx
// A simple, single-file React + TypeScript component that lets a user enter their name
// and write a post. Typing `@me` inside the post will render the entered name in the
// live preview and on submit.

// Usage: import PostComposer from './PostComposer'; and render <PostComposer />

export default function PostComposer() {
  const [name, setName] = useState<string>("");
  const [post, setPost] = useState<string>("");
  const [submittedPosts, setSubmittedPosts] = useState<Array<{ id: number; author: string; content: string }>>([]);
  const maxLength = 1000;

  function renderWithName(raw: string, displayName: string) {
    // Replace all occurrences of `@me` with the user's name (escaped for safety)
    const safeName = displayName || "(you)";
    return raw.split("@me").map((part, idx) => (
      // We'll return React nodes to allow simple highlighting of the name
      <React.Fragment key={idx}>
        {part}
        {idx < raw.split("@me").length - 1 && (
          <span className="font-semibold text-indigo-700">{safeName}</span>
        )}
      </React.Fragment>
    ));
  }

  function handleSubmit(e?: React.FormEvent) {
    e?.preventDefault();
    if (!post.trim()) return;
    const finalContent = post.replace(/@me/g, name || "(you)");
    const newPost = {
      id: Date.now(),
      author: name || "Anonymous",
      content: finalContent,
    };
    setSubmittedPosts((s) => [newPost, ...s]);
    setPost("");
  }

  return (
    <div className="max-w-3xl mx-auto p-4">
      <div className="bg-white shadow-md rounded-2xl p-6">
        <h2 className="text-2xl font-bold mb-4">Write a post</h2>

        <label className="block mb-2">
          <div className="text-sm text-gray-600">Your name</div>
          <input
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Enter your name"
            className="mt-1 w-full rounded-lg border p-2 focus:outline-none focus:ring-2 focus:ring-indigo-300"
            aria-label="Your name"
          />
        </label>

        <form onSubmit={handleSubmit} className="mt-4">
          <label className="block mb-2">
            <div className="text-sm text-gray-600">Post (use <code>@me</code> to mention your name)</div>
            <textarea
              value={post}
              onChange={(e) => setPost(e.target.value)}
              placeholder="Write something..."
              maxLength={maxLength}
              rows={5}
              className="mt-1 w-full rounded-lg border p-3 focus:outline-none focus:ring-2 focus:ring-indigo-300"
              aria-label="Post content"
            />
          </label>

          <div className="flex items-center justify-between mt-2">
            <div className="text-sm text-gray-500">{post.length}/{maxLength} characters</div>
            <div className="flex gap-2">
              <button
                type="button"
                onClick={() => {
                  setPost((p) => p + (p ? "\n@me" : "@me"));
                }}
                className="px-3 py-1 rounded-md border hover:bg-gray-50"
              >
                Insert @me
              </button>
              <button
                type="submit"
                className="px-4 py-1 rounded-md bg-indigo-600 text-white font-medium hover:bg-indigo-700"
              >
                Post
              </button>
            </div>
          </div>
        </form>

        <div className="mt-6">
          <h3 className="text-lg font-semibold mb-2">Live preview</h3>
          <div className="rounded-md border p-4 bg-gray-50 min-h-[60px]">
            {post ? (
              <div className="prose">
                <div className="text-sm text-gray-600 mb-1">Author: <span className="font-medium">{name || "Anonymous"}</span></div>
                <div>{renderWithName(post, name)}</div>
              </div>
            ) : (
              <div className="text-sm text-gray-400">Your post preview will appear here.</div>
            )}
          </div>
        </div>

        {submittedPosts.length > 0 && (
          <div className="mt-8">
            <h3 className="text-lg font-semibold mb-3">Submitted posts</h3>
            <div className="space-y-4">
              {submittedPosts.map((p) => (
                <article key={p.id} className="border rounded-lg p-4 bg-white">
                  <div className="text-sm text-gray-600 mb-2">{p.author}</div>
                  <div className="whitespace-pre-wrap">{p.content}</div>
                </article>
              ))}
            </div>
          </div>
        )}
      </div>

      <div className="text-right text-xs text-gray-400 mt-3">Tip: type <code>@me</code> in the post to mention yourself in the preview.</div>
    </div>
  );
}
